*{font-family:'Open Sans', sans-serif !important}body{margin:0;height:100vh;width:100vw;padding:0px;background-color:#FAFAFA}.app{display:flex;justify-content:center;flex-direction:column;grid-gap:16px;gap:16px;align-items:center}.app__todos{display:flex;grid-gap:16px;gap:16px;justify-content:center;flex-wrap:wrap;width:90%}.todo-form{display:flex;align-items:center;grid-gap:16px;gap:16px;justify-content:center;width:100%}.todo-form__textblock{border:1px solid rgba(0,0,0,0.42);box-shadow:2px 2px 2px 2px rgba(0,0,0,0.2);border-radius:4px;padding:8px;width:100%;max-width:600px;display:flex;flex-direction:column;grid-gap:8px;gap:8px;background-color:#fff}.todo-form__textarea{resize:none;min-height:48px;border:none;overflow:auto;outline:none;box-shadow:none;font-size:14px}.todo-form__input{font-size:16px;border:none;outline:'none';font-weight:bold !important}.todo-form__tools{display:flex;justify-content:flex-end}.todo-form__cancel{color:#EF5350}.create-button{height:168px;display:flex;justify-content:center;align-items:center}.item-todo{border:1px solid rgba(0,0,0,0.22);border-radius:4px;padding:8px;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;min-width:240px;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;display:flex;flex-direction:column;grid-gap:4px;gap:4px;transition:all 0.2s ease-in-out;max-height:350px;max-width:350px;overflow:auto;background-color:#fff}.item-todo:hover{cursor:pointer;box-shadow:2px 2px 2px 2px rgba(0,0,0,0.2);-webkit-transform:translate(2px);transform:translate(2px)}.item-todo:hover .item-todo__tools{opacity:1}.item-todo__header{font-weight:bold}.item-todo__body{white-space:pre-line}.item-todo__tools{opacity:0;display:flex;justify-content:flex-end;grid-gap:8px;gap:8px;margin-top:8px}.item-todo__tools .MuiSvgIcon-root{font-size:22px !important}.item-todo__tools .MuiIconButton-root{width:34px;height:34px}.item-todo__done:hover{color:#4CAF50}.item-todo__delete:hover{color:#EF5350}.item-todo__icon{font-size:16px}.item-todo__complete{background-color:#E8F5E9;border:1px solid #4CAF50}.app-bar{width:100vw !important}.sorted-list{display:flex;grid-gap:16px;gap:16px;flex-wrap:wrap;justify-content:center}.dialog-form{display:flex;align-items:center;grid-gap:16px;gap:16px;justify-content:center}.dialog-form__textblock{padding:16px 24px;width:600px;display:flex;flex-direction:column;grid-gap:8px;gap:8px}.dialog-form__textarea{resize:none;min-height:48px;border:none;overflow:auto;outline:none;box-shadow:none;font-size:14px}.dialog-form__input{font-size:16px;border:none;outline:'none';font-weight:bold !important}.dialog-form__tools{display:flex;justify-content:flex-end}.dialog-form__cancel{color:#EF5350}@media (max-width: 520px){.item-todo__tools{opacity:1}}

